import axios from 'axios';
import { INewsData } from '../types/api-types';

const instanceAxios = axios.create({
  baseURL: 'https://newsapi.org/v2/',
  headers: {
    'x-api-key': 'f36184a7957445a382ad24af237cba3e',
  },
});
export const newsAPI = {
  getNews(search: string, page: number = 1) {
    return instanceAxios
      .get<INewsData>(`everything?language=ru&q=${search}&page=${page}`)
      .then(res => res.data);
  },
};
