import React, { FC } from 'react';
import cl from './News.module.scss';

const categoryData = [
  {
    value: 'IT',
  },
  {
    value: '',
  },
  {
    value: '',
  },
  {
    value: '',
  },
  {
    value: '',
  },
];

interface IPropsCategory {
  query: string;
  setQuery: (query: string) => void;
}

const CategoryNews: FC<IPropsCategory> = React.memo(props => {
  return (
    <div className={cl.category}>
      <form>
        {categoryData.map(item => (
          <CategoryItem
            key={item.value}
            query={props.query}
            value={item.value}
            setQuery={props.setQuery}
          />
        ))}
      </form>
    </div>
  );
});

interface IPropsItem {
  value: string;
  query: string;
  setQuery: (query: string) => void;
}

const CategoryItem: FC<IPropsItem> = React.memo(({ value, query, setQuery }) => {
  const categoryHandler = () => {
    setQuery(value);
  };
  return (
    <label className={cl.radio}>
      <input
        type='radio'
        value={value}
        checked={value === query}
        onChange={categoryHandler}
        name={'news'}
      />
      <div className={cl.mock}></div>
      <span>{value}</span>
    </label>
  );
});

export default CategoryNews;
