import { ThunkType } from '../../types/reducers-types';
import { ActionsTypes } from '../../types/reducers-types';
import { newsAPI } from '../../api/new-api';
import { INewsArticles } from '../../types/api-types';

type TPending = 'pending' | 'ready';
type TError = 429 | 426;
let initialState = {
  newArticlesLoad: false as boolean,
  articles: [] as [] | INewsArticles[],
  pending: 'pending' as TPending,
  error: null as TError | null,
};

export type initialStateTypes = typeof initialState;

const newsReducer = (
  state = initialState,
  action: ActionsTypes<typeof newsActions>,
): initialStateTypes => {
  switch (action.type) {
    case 'NEWS_RECEIVED':
      return {
        ...state,
        articles: [...state.articles, ...action.payload.articles],
      };
    case 'NEWS_CATEGORY_RECEIVED':
      return {
        ...state,
        articles: [...action.payload.articles],
      };
    case 'SET_PENDING':
      return {
        ...state,
        pending: action.pending,
      };
    case 'SET_ERROR':
      return {
        ...state,
        error: action.error,
      };
    case 'SET_ARTICLES_LOAD':
      return {
        ...state,
        newArticlesLoad: action.newArticlesLoad,
      };

    default:
      return state;
  }
};

export const newsActions = {
  newsReceived: (articles: INewsArticles[]) =>
    ({
      type: 'NEWS_RECEIVED',
      payload: { articles },
    } as const),
  newsCategoryReceived: (articles: INewsArticles[]) =>
    ({
      type: 'NEWS_CATEGORY_RECEIVED',
      payload: { articles },
    } as const),
  changePending: (pending: TPending) =>
    ({
      type: 'SET_PENDING',
      pending,
    } as const),
  setError: (error: TError | null) =>
    ({
      type: 'SET_ERROR',
      error,
    } as const),
  setLoadNews: (newArticlesLoad: boolean) =>
    ({
      type: 'SET_ARTICLES_LOAD',
      newArticlesLoad,
    } as const),
};
export const getNews =
  (page: number, query: string): ThunkType =>
  async dispatch => {
    try {
      dispatch(newsActions.changePending('pending'));
      const data = await newsAPI.getNews(query, page);
      dispatch(newsActions.newsReceived(data.articles));
      dispatch(newsActions.setLoadNews(true));
    } catch (e) {
      errorResponse(e, dispatch);
    } finally {
      dispatch(newsActions.changePending('ready'));
    }
  };
export const getNewsCategory =
  (page: number, query: string): ThunkType =>
  async dispatch => {
    dispatch(newsActions.setLoadNews(false));
    try {
      dispatch(newsActions.setError(null));
      const data = await newsAPI.getNews(query, page);
      dispatch(newsActions.newsCategoryReceived(data.articles));
      dispatch(newsActions.setLoadNews(true));
    } catch (e) {
      errorResponse(e, dispatch);
    }
  };

export default newsReducer;

function errorResponse(e: any, dispatch: any) {
  // @ts-ignore
  const code = parseInt(e.message.match(/\d+/));
  if (code === 429) {
    dispatch(newsActions.setError(429));
  } else if (code === 426) {
    dispatch(newsActions.setError(426));
  }
}
