import React, { FC, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { compose } from 'redux';
import { useInView } from 'react-intersection-observer';
import { v4 as uuidv4 } from 'uuid';

import { withAuthRedirect } from '../../hoc/withAuthRedirect';
import { getNews, getNewsCategory } from '../../redux/reducer/news-reducer';
import { useAppSelector } from '../../types/reducers-types';
import { LoaderNews } from '../../components/utils/LoaderNews/LoaderNews';
import NewsPost from './NewsPost';
import cl from './News.module.scss';
import CategoryNews from './CategoryNews';

const News: FC = React.memo(() => {
  const loadNews = useAppSelector(state => state.news.newArticlesLoad);
  const newsList = useAppSelector(state => state.news.articles);
  const isError = useAppSelector(state => state.news.error);
  const { ref, inView } = useInView({ rootMargin: '250px', threshold: 0 });
  const mockNewsList = new Array(10).fill('news');
  const [page, setPage] = useState(1);
  const [query, setQuery] = useState('IT');
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getNews(page, query));
  }, [query]);

  useEffect(() => {
    if (inView) {
      dispatch(getNews(page, query));
      setPage(prev => prev + 1);
    }
  }, [inView]);

  useEffect(() => {
    setPage(1);
    dispatch(getNewsCategory(page, query));
  }, [query]);

  return (
    <div className={cl.newsPage}>
      <CategoryNews query={query} setQuery={setQuery} />
      <div>
        {loadNews ? (
          newsList.map(item => <NewsPost key={uuidv4()} {...item} />)
        ) : isError === 429 ? (
          <div className={cl.error}>
             .        
          </div>
        ) : (
          mockNewsList.map((item, i) => <LoaderNews key={i} />)
        )}
        {!isError && <div ref={ref}></div>}
      </div>
    </div>
  );
});

export default compose(withAuthRedirect)(News);
