import React, { FC } from 'react';
import cl from './News.module.scss';
import newsMock from '../../assets/img/newsmock.jpg';

interface IProps {
  author: string | null;
  description: string;
  publishedAt: string;
  title: string;
  url: string;
  urlToImage: string;
}

const NewsPost: FC<IProps> = React.memo(props => {
  const date = new Date(props.publishedAt).toLocaleDateString();

  return (
    <div className={cl.item}>
      <div className={cl.image}>
        <a href={props.url} target={'_blank'}>
          <img src={props.urlToImage ? props.urlToImage : newsMock} alt='newImage' />
        </a>
      </div>
      <div className={cl.body}>
        <a href={props.url} target={'_blank'}>
          <h3>{props.title}</h3>
        </a>
        <p>{props.description}</p>
        <div className={cl.data}>
          <div className={cl.time}>{date}</div>
          <div className={cl.author}>{props.author ? props.author : ' '}</div>
        </div>
      </div>
    </div>
  );
});

export default NewsPost;
