import React from 'react';
import cl from './LoaderNews.module.scss';
export const LoaderNews = () => {
  return (
    <div className={cl.item}>
      <div className={cl.image}></div>
      <div className={cl.body}>
        <h3>{}</h3>
        <p>{}</p>
        <div className={cl.data}>
          <div className={cl.time}></div>
          <div className={cl.author}></div>
        </div>
      </div>
    </div>
  );
};
